
const Howl = require('howler')
const fisica = require('fisica')
const PIXI = require('pixi')
const Place = require('./Place.js')
const GameManager = require('./GameManager.js')

const methodTypes = [
  'init',
  'update',
  'keyDown',
  'keyPress',
  'keyUp',
  'mouseDown',
  'mouseUp',
  'globalMouseDown',
  'globalMouseUp',
  'mouseDragStart',
  'mouseDragEnd',
  'collision']

let IMAGE_CACHE = {}

class GameObject {
  constructor (data) {
    this.sounds = {}
    this.methods = {}
    Object.keys(data).forEach((key) => {
      this.add(key, data[key])
    })
    if (!this.ubication) {
      this.addUbication({
        position: [0, 0],
        rotation: 0,
        scale: 1
      })
    }
  }

  add (type, data) {
    if (type === 'script') this.addScript(data)
    else if (type === 'sprite') this.addSprite(data)
    else if (type === 'collider') this.addCollider(data)
    else if (type === 'sound') this.addSound(data)
    else if (type === 'ubication') this.addUbication(data)
  }

  addSound (data) {
    this.sounds[data.name] = new Howl.Howl({src: data.src})
  }

  addUbication (data) {
    this.ubication = new Place({
      gameObject: this,
      position: data.position,
      scale: data.scale,
      rotation: data.rotation
    })
  }

  addCollider (data) {
    this.collider = new fisica.Body(data.type, data.config)
    this.collider.gameObject = this
    GameManager.engine.add(this.collider)
  }

  addScript (data) {
    methodTypes.forEach((method) => {
      if (data[method]) {
        if (this.methods[method]) {
          this.methods[method].push(data[method])
        } else {
          this.methods[method] = []
          this.methods[method].push(data[method])
        }
      }
    })
  }

  addSprite (data) {
    if (data.name && data.src) {
      IMAGE_CACHE[data.name] = new PIXI.Sprite(PIXI.Texture.fromImage(data.src))
      this.sprite = IMAGE_CACHE[data.name]
    } else if (data.name && !data.src) {
      this.sprite = IMAGE_CACHE[data.name]
    } else if (!data.name && data.src) {
      this.sprite = new PIXI.Sprite(PIXI.Texture.fromImage(data.src))
    }

    if (data.anchor) {
      this.sprite.anchor.x = data.anchor
      this.sprite.anchor.y = data.anchor
    } else {
      this.sprite.anchor.x = 0.5
      this.sprite.anchor.y = 0.5
    }

    if (data.scale) {
      this.sprite.scale.x = data.scale
      this.sprite.scale.y = data.scale
    }

    GameManager.stage.addChild(this.sprite)
  }

  run (method, data) {
    if (this.methods[method]) this.methods[method].forEach((x) => x(this, data))
  }

  destroy () {
    if (this.sprite) GameManager.stage.removeChild(this.sprite)
    if (this.collider) GameManager.engine.destroy(this.collider)
  }

  update () {
    this.ubication.update()
  }
}

module.exports = GameObject



