
const fisica = require('fisica')
const PIXI = require('pixi')

const engine = new fisica.Engine()
const app = PIXI.autoDetectRenderer(window.innerWidth, window.innerHeight)

class GameManager {
  constructor () {
    this.stage = new PIXI.Stage(0x222222)
    this.engine = engine
    this.paused = false
    this.time = 1000 / 60
  }

  setScene (scene) {
    this.scene = scene
  }

  run (name) {
    this.scene.run(name)
  }

  init () {
    window.addEventListener('load', function () {
      document.body.appendChild(app.view)
    })
    this.interval = setInterval(() => {
      this.scene.run('update')
      this.scene.update()
      this.engine.update()
      app.render(gameManager.stage)
      if (this.paused) this.time = 0
    }, this.time)
  }
}

// GLOBAL EVENTS

let keys = {}

document.addEventListener('mousedown', function () {
  if (!gameManager.paused) gameManager.scene.run('globalMouseDown')
})

document.addEventListener('mouseup', function () {
  if (!gameManager.paused) gameManager.scene.run('globalMouseUp')
})

document.addEventListener('keydown', function (event) {
  keys[event.key] = true
  if (!gameManager.paused) gameManager.scene.run('keyDown', event.key)
})

document.addEventListener('keyup', function (event) {
  keys[event.key] = false
  if (!gameManager.paused) gameManager.scene.run('keyUp', event.key)
})

setInterval(function () {
  Object.keys(keys).some(function (key) {
    if (keys[key]) {
      if (!gameManager.paused) gameManager.scene.run('keyPress', keys)
      return true
    }
  })
}, 20)

const gameManager = new GameManager()
module.exports = gameManager



